.home .col-sm-6 {
    padding-left: 0px !important;
  }
  
  .home-small-img {
    height: 100% !important; 
    width: 100% !important;
  }
  
  .home-small-img img {
    height: 100% !important;
    background-size:cover;
    background-position:50% 50%;
  }
  
  .mask-overlay {
    position: relative;
    display: inline-block;
    overflow:hidden;
    -webkit-transition: all 1s ease-out;
    -moz-transition: all 1s ease-out;
    -o-transition: all 1s ease-out;
    -ms-transition: all 1s ease-out;
    transition: all 1s ease-out;
  }
  
  .mask-overlay img {
    width: 100%;
    transition: transform 1s ease-in-out;
    transform-origin: 50% 50%
  }
  
  .mask-overlay:hover img {
    transform: scale(1.2);
    width: 100%;
  }
  
  .mask-overlay:hover {
    -webkit-transition: all 1s ease-in;
    -moz-transition: all 1s ease-in;
    -o-transition: all 1s ease-in;
    -ms-transition: all 1s ease-in;
    transition: all 1s ease-in;
    opacity:0.7;
  }
  
  .mask-overlay:focus {
    animation-play-state: paused;
    opacity: 0.7;
  }
  
  .mask-overlay:after{
    content: "";
    background: -webkit-linear-gradient(130deg, rgba(67,9,242,0.35) 0%, rgba(242,0,238,0.44) 90%);
    background: linear-gradient(130deg, rgba(67,9,242,0.35) 0%, rgba(242,0,238,0.44) 90%);
    position: absolute;
    display: flex;
    width: 100%;
    height: 100%;
    top: 0;
    bottom: 0;
  }
  
  .postHome {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;
    opacity: 1;
    -webkit-transition: none;
    -moz-transition: none;
    -o-transition: none;
    -ms-transition: none;
    transition: none;
  }
  
